'use client';

import { useEffect, useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Table, TableHeader, TableRow, TableHead, TableBody, TableCell } from '@/components/ui/table';
import { toast } from 'sonner';

interface StaleMedication {
  id: string;
  name: string;
  barcode: string;
  createdAt: string;
  // Add other relevant fields if needed
}

export function StaleMedicationsAlert() {
  const [staleMedications, setStaleMedications] = useState<StaleMedication[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchStaleMedications = async () => {
      try {
        const res = await fetch('/api/medications/stale-medications');
        if (res.ok) {
          const data = await res.json();
          setStaleMedications(data);
        } else {
          toast.error('Erreur lors du chargement des m√©dicaments non vendus.');
        }
      } catch (error) {
        console.error('Failed to fetch stale medications:', error);
        toast.error('Erreur r√©seau lors du chargement des m√©dicaments non vendus.');
      } finally {
        setLoading(false);
      }
    };

    fetchStaleMedications();
  }, []);

  if (loading) {
    return <p>Chargement des alertes de m√©dicaments non vendus...</p>;
  }

  if (staleMedications.length === 0) {
    return null; // Don&apos;t show the card if there are no stale medications
  }

  return (
    <Card className="border-orange-500">
      <CardHeader>
        <CardTitle className="text-orange-600">M√©dicaments non vendus (90+ jours)</CardTitle>
      </CardHeader>
      <CardContent>
        <p className="mb-2">Les m√©dicaments suivants n&apos;ont pas √©t√© vendus depuis 90 jours ou plus :</p>
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>M√©dicament</TableHead>
              <TableHead>Code-barres</TableHead>
              <TableHead>Date d&apos;ajout</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {staleMedications.map((med) => (
              <TableRow key={med.id}>
                <TableCell>{med.name}</TableCell>
                <TableCell>{med.barcode}</TableCell>
                <TableCell>{new Date(med.createdAt).toLocaleDateString()}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </CardContent>
    </Card>
  );
}
